import React, { Component } from 'react'
import Numeral from 'react-numeral';
import { connect } from 'react-redux';
import { CalcCrow, capitalize } from '../../../utils/utils';
import RawTitleValue from '../title_value/title_value';

class TourSiteDetails extends Component {
    render() {
        const { tour } = this.props;
        return (
            <div>
                <div className='tour_site_card_details_price_like flex'>
                    <div className='hotel_card_details_star'>
                        <h2>{tour.Name}</h2>
                        <p>{this.props?.currencies.filter((e) => e.id === tour.Currency)[0]?.Name} <Numeral value={tour.Price} format={'0.0a'} /></p>
                    </div>
                    {tour.SiteWeb && <a href={tour.SiteWeb} target='_blank' rel='noreferrer'>
                        <div className='action_button flex'>
                            <i className='fa fa-globe' />
                            <h6>Visit website</h6>
                        </div>
                    </a>}

                </div>
                {tour.History && <>
                    <div className='divider' />
                    <p>{tour.History}</p>
                    <div className='divider' />
                </>}
                <RawTitleValue title='Type' value={`${tour.TourismType[capitalize(this.props.currentLanguage.code)]}`} />
                <RawTitleValue title='Distance from your location' value={`${CalcCrow(tour.Latitude, tour.Longitude, this.props.userLocation?.lat, this.props.userLocation?.lon)} Kilometers away`} />
                <RawTitleValue title='Address' value={`${tour.Address}`} />
                <RawTitleValue title='Built in' value={`${tour.BuiltIn}`} />
                <div className='divider' />
                <p>{tour.Description}</p>
                <div className='divider' />
            </div>
        )
    }
}


const mapStateToProps = (storeOf) => ({
    currentLanguage: storeOf.madoshome.currentLanguage,
    currencies: storeOf.madoshome.currencies,
    userLocation: storeOf.madoshome.userLocation,
});

export default connect(mapStateToProps)(TourSiteDetails);
